.successMessage > .Message > p > h2{
    
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 1.5rem;
    margin-top: 5vh;
    display: flex;
    
}

.successMessage > .Message > p > h2 > img {
    margin-left: 1vh;
}

@keyframes rotation {
    100%{ transform:rotatey(360deg); }
  }



p > h6 {
    font-size: 2rem;
    font-weight: 600;
}

.successMessage{
    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
}

.Message > img {
    margin-top: 2vh;
    animation: rotation 4s infinite linear;

}

.Message {
    margin-top: 10vh;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.okaybtn {
    background-color: rgb(253, 144, 55);
    text-decoration: none;
    font-weight: 600;
    padding: 5px;
    color: white;
    border-radius: 10px;
    letter-spacing: 0.15rem;
    padding-left: 2vh;
    padding-right: 2vh;
}

.click-me {
    margin-top: 2vh;
}

.okaybtn:hover {
    color: rgb(65, 65, 65);
    border: 0.5px solid grey;
}

