.slider2 {
    width: 300px;
    height: 300px;
    background-color: yellow;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0px;
    text-align: center;
    overflow: hidden;
    margin-top: 2vh;
    margin-right: 0.5vh;
    border-radius: 15px;
  }
  .image-container {
    width: 1500px;
    background-color: pink;
    height: 300px;
    clear: both;
    position: relative;
    -webkit-transition: left 2s;
    -moz-transition: left 2s;
    -o-transition: left 2s;
    transition: left 2s;
  }
  .slide2 {
    float: left;
    margin: 0px;
    padding: 0px;
    position: relative;
    
  }
  #slide-1:target ~ .image-container {
    left: 0px;
  }
  #slide-2:target ~ .image-container {
    left: -300px;
  }
  #slide-3:target ~ .image-container {
    left: -600px;
  }
  .buttons {
    position: relative;
    top: -20px;
  }
  .buttons a {
    display: inline-block;
    height: 15px;
    width: 15px;
    border-radius: 50px;
    background-color: rgb(255, 255, 255);
    margin-left: 5px;
    margin-right: 5px;
  }
  .image-container > img {
    height:300px;
    width: 300px;
    

  }

  .info {
    font-size: 1.8vh;
