}

.blink_me > p {

  margin-left: 25%;
  margin-right: 25%;

}


/* Animation */
.blink_me > p {
  -webkit-animation: animated-text 6s linear 1s 1 normal both,
             animated-cursor 600ms linear infinite;
          animation: animated-text 6s linear 1s 1 normal both,
             animated-cursor 600ms linear infinite;
}

/* text animation */

@-webkit-keyframes animated-text{
  from{width: 0;}
  to{width: 606px;}
}

@keyframes animated-text{
  from{width: 0;}
  to{width: 606px;}
}

/* cursor animations */

@-webkit-keyframes animated-cursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
}

@keyframes animated-cursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
}

