.home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
    

}
.header_two {
    
	width: 100%;
	height: 3vw;
	background: black;
	color: #96f56bb4;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1.5vw;
	text-transform: uppercase;
	letter-spacing: 5px;
	font-weight: 400;
    margin-bottom: 0;
}


.home_container {
    display: flexbox;
    flex-wrap: wrap;
    justify-content: space-evenly;
    height: 100vh;
    align-items: center;
    margin-top: 0;
  
    
}






  .about_container > h1 {
      color: white;
      justify-content: center;
  }
  
  
    


.home_image {
    
    width: 100%;
    z-index: 1;
    margin-bottom: -180px;
}


.home_container > .vincenttitle > h2 {
    justify-content: center;
    text-align: center;
   
    color: antiquewhite;
    font-size: 5vh;
    letter-spacing: 1vh;
    margin-bottom: 3vh;
    padding-top: 15vh;
    
    
}

.home_container > p {
    margin-left: 15%;
    margin-right: 15%;
    text-align: center;
    color: antiquewhite;
    margin-top: 5vh;
    font-size: 2.2vh;
   
}

.home_container > .kit {
    justify-content: center;
    align-items: center;
    text-align: center;
}

.kit > img {
    border-radius: 50%;
    margin-top: 3vh;
} 

.home_containertwo {
    height: 100vh;
    display: flex;
    flex-direction: row;
    background: transparent;
}

.left {
    display: flex;
    flex-direction: column;
    width: 50%;
    text-align: center;
    justify-content: center;
}

.right {
    text-align: center;
    width: 50%;
    justify-content: center;
    margin-top: 20%;
    color: rgb(24, 24, 24);
    position: relative;
    margin-right: 5vh;
    font-weight: 700;
    letter-spacing: 2px;
    font-size: 2.4vh;
}



.aspchem {
  background-color: #292929;
  height: fit-content;
    justify-content: center;
    text-align: center;
    margin-bottom: 5vh;
    margin-top: -15%;
    color: white;
}

.aspchem > p {
    font-size: 3.4vh;
    font-weight: 700;
    padding-top: 1.5vh;
    padding-bottom: 1.5vh;
}

.logoimage > img {
    margin-top: 3vh;
}


.mainOne {
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    justify-content: space-evenly;
    flex: 0.5;
    margin-top: 2vh;
}

.getstarted {
    display: flex;
    flex-direction: column;
}

.getstarted > h3 {
    margin-bottom: 2vh;
}

.logo1 {
    justify-content: center;
    text-align: center;
    margin-top: 50px;
    font-size: 3vh;
    margin-left: -3vh;
}


@media(max-width:900px){
    .right{
        font-size: 1.6vh;
        margin-left: 2vh;
        align-items: center;
        text-align: center;
        top: 5vh;
    }
    .logoimage> img{
        height: 150;
        width: 180;
    }
    .home_container > p{
        font-size: 1.25vh;
    }
}