
.mainOne {
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    justify-content: space-evenly;
    flex: 0.5;
    color: aliceblue;
    margin-top: 0;
}

.getstarted {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.footerSection {
    background-color: rgb(58, 58, 58);

}

.getstarted > .span {
    margin: 1vh;
    text-decoration: none;
    color: white;
}

.getstarted > .span:hover {
    color: #96f56bb4;
    cursor: pointer;
}

