@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');

*{
    font-family: 'Open Sans', sans-serif;
}

.sections {
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 50%;
    margin-top: 8vh;
    margin-left: 15px;
    margin-right: 15px;
}

.section_left {
    
    justify-content: center;
    align-items: center;
    display: block;
    text-align: center;
    margin: 5px;
    width: 50%;
}

.Containervalues {
    height: 100vh;
    width: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-image: url('../src/Images/building.jpg');   
    background-size: cover;
    background-repeat: no-repeat;
}

.sections > p {
    justify-content: center;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.section_right {
    
    justify-content: center;
    align-items: center;
    display: block;
    text-align: center;
    margin: 5px;
    width: 50%;
    
}

.section_left > p > h2  {
    background-color: #000000;
    height: 5vh;
    justify-content: center;
    border-radius: 10px;
    color: #96f56bb4;
    letter-spacing: 1vh

}

.section_right > p > h2 {
    background-color: #000000;
    height: 5vh;
    justify-content: center;
    border-radius: 10px;
    color: #96f56bb4;
    letter-spacing: 1vh

}

.globe > p > img {
    margin: 3vh;
    width: auto;
    max-height: 25vh;
    margin-left: 20vh;
    margin-right: 20vh;
}

.Containervalues > span {
    justify-content: center;
    text-align: center;
    color: black;
}

.heading_ourvalues {
    font-size: 5vh;
    font-weight: bolder;
    padding-top: 10vh;
    letter-spacing: 5px;
    
}

h5 {
    font-size: 3vh;
    font-weight: 900;
    margin-top: 1vh;
    color: rgb(0, 0, 0);
    font-style: italic;
    background-color: rgba(206, 247, 158, 0.568);
    border-radius: 5px;
}

.globee > .imgtwo {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: 4vh;
    animation: up-down 2s ease-in-out infinite alternate-reverse both;

}

.globee {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

@-webkit-keyframes up-down {
    0% {
        transform: translateY(10px);
    }
    100% {
        transform: translateY(-10px);
    }
}

@keyframes up-down {
    0% {
        transform: translateY(10px);
    }
    100% {
        transform: translateY(-10px);
    }
}

.globee > img {
    margin-left: 45px;
}

.heading_ourvalues > p > span {
    color: #000000;
}


@media(max-width:900px){
    .sections{
        flex-direction: column;
        justify-content: center;
        text-align: center;
        align-items: center;
    }
}