@import url('https://fonts.googleapis.com/css2?family=Cinzel&display=swap');

.topContent {
    color: white;
    
}

.topContent_container {
    position: absolute;
    top: 200px;
    left: 10%;
    right: 10%;
    align-items: center;
    text-align: center;
    justify-content: center;

}

.blink_me > p {
  border-right: solid 5px rgba(158, 158, 158, 0.75);
  white-space: nowrap;
  overflow: hidden;    
  font-family: 'Source Code Pro', monospace;  
  font-size: 28px;
  color: rgb(241, 12, 12);
  
  text-align: center;
  align-items: center;
}

.blink_me > p {

  margin-left: 25%;
  margin-right: 25%;

}


/* Animation */
.blink_me > p {
  animation: animated-text 6s linear 1s 1 normal both,
             animated-cursor 600ms linear infinite;
}

/* text animation */

@keyframes animated-text{
  from{width: 0;}
  to{width: 606px;}
}

/* cursor animations */

@keyframes animated-cursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
}

.endtag {
    margin-top: 25px;
    margin-bottom: 10px;
}

.blink_me {
    font-style: italic;
    font-size: 22px;
    font-weight: bold;
    justify-content: center;
    text-align: center;
    color: rgb(240, 88, 88);
    align-items: center;

    letter-spacing: 1px;
    align-items: center;
    

}


.blink_me > p {
  animation: animated-text 4s steps(60,end) 1s 1 normal both,
  
}

@keyframes animated-text{
  from{width: 0;}
  to{width: 606px;}
}











.heading {    /* For increasing performance 
                       ID/Class should've been used. 
                       For a small demo 
                       it's okaish for now */
  animation: showup 4s infinite;
  font-family: 'Cinzel', sans-serif;
}



@keyframes showup {
    0% {opacity:0;}
    20% {opacity:1;}
    80% {opacity:1;}
    100% {opacity:0;}
}

@keyframes slidein {
    0% { margin-left:-800px; }
    20% { margin-left:-800px; }
    35% { margin-left:0px; }
    100% { margin-left:0px; }
}

@keyframes reveal {
    0% {opacity:0;width:0px;}
    20% {opacity:1;width:0px;}
    30% {width:355px;}
    80% {opacity:1;}
    100% {opacity:0;width:355px;}
}




svg text {
    font-family: Lora;
    letter-spacing: 15px;
    stroke: #aff066;
    font-size: 12vh;
    font-weight: 400;
    stroke-width: 2;
    align-items: center;
    justify-content: center;
    text-align: center;
    left: 50%;
    animation: textAnimate 5s infinite alternate;
    
  }
  
  @keyframes textAnimate {
    0% {
      stroke-dasharray: 0 50%;
      stroke-dashoffset:  20%;
      fill:hsl(0, 0%, 15%)
  
    }
    
    100% {
      stroke-dasharray: 50% 0;
      stroke-dashoffstet: -20%;
      fill: hsla(189, 68%, 75%,0%)
    }
    
  }
  
  
  

  @media(max-width:900px){
    .heading{
      font-size: 2vh;
    }
    .patterns{
      font-size: 2vh;
    }
    .vincento{
      font-size: 8vh;
    }
    .blink_me >p{
      text-align: center;
      align-items: center;
      justify-content: center;
      margin-left: -13vh;
      font-size: 2vh;
    }
  }