.payment {
    background-color: white;
     
}

.payment_container > h2 {
    text-align: center;
    font-weight: 400;
    padding: 10px;
    background-color: aliceblue;
    border-bottom: 1px solid lightgray;
}

.payment_container > h2 a {
    text-decoration: none;
}

.payment_section {
    display: flex;
    padding: 3px;
    margin: 10px;
    border-bottom: 1px solid lightgray;
}

.payment_title {
    flex: 0.15;
}

.payment_address, .payment_items {
    flex: 0.6;
    margin-left: 10px;
}

.mb-3 {
    margin: 5px;
}

.payment_priceContainer {
    justify-content: center;
    align-items: center;
    display: flex;
}

.col-12 > button {
    justify-content: center;
    align-items: center;
    display: flex;
    margin-left: 5px;
    background-color: rgb(99, 98, 98);
    color: white;

}

.col-12 > button:hover {
    background-color: rgb(43, 43, 43);
}

.order-btn{
    padding: 5px;
    background-color: rgb(0, 217, 255);
    color: black;
    text-decoration: none;
    border-radius: 10px;
    
}