.topContainer {
   
      height: 100vh;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.5);
      width: 100%;
      height: fit-content;
  
  }
  
  .sliderimg{
    width: 100%;
    height: 100vh;
    object-fit: cover
  }
  
  .slide-container {
    scroll-snap-type: x mandatory;
    overflow-x: scroll;
    display: flex;
  }
 
  .slide {
    scroll-snap-align: start;
    min-width: 100vh;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
