.subtotal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 200px;
    height: 80px;
    padding: 1px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
    align-items: center;
    font-size: 14px;
}




.subtotal > button {
    background: #71c2f5;
    border-radius: 3px;
    width: 70%;
    height: 22px;
    border: 1px solid;
    margin-bottom: 10px;
    
    color: #111;
    font-size: 12px;
    padding: 1px;
    font-weight: 600;
}

.subtotal > button:hover {
    letter-spacing: 0.5px;
    background-color: #80c5f0;
    cursor: pointer;
    

}