.our-customer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-image: url('../src/Images/pixx.jpg');   
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}


.section_one {
    display: flex;
    margin-top: 5vh;
    animation: up-down 2s ease-in-out infinite alternate-reverse both;

}

.section_two > p > h2 {
    font-size: 5vh;
    font-weight: 500;
    margin-top: 3vh;
    margin-bottom: 3vh;
    
}

.section_two > p {
    font-size: 2.5vh;
    margin-left: 5vh;
    margin-right: 5vh;
    color: rgb(255, 255, 255);
    
    
}
.section_two > p > h2 {
    font-size: 4vh;
    margin-left: 5vh;
    margin-right: 5vh;
    background-color: black;
    color: white;
    border-radius: 7px;
    
}

.section_two > h5 {
    margin-top: 2vh;
    background-color: rgb(55, 207, 174);
    width: fit-content;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.section_two > span > p {
    font-size: 2.5vh;
    margin-top: 2vh;
    color: rgb(255, 254, 254);
}

.partners > h5 {
    margin: 2vh;
    background-color: #2020207e;
    border-radius: 8px;
    width: max-content;
    text-align: -webkit-center;
    align-items: center;
    justify-content: center;
    padding-left: 1.5vh;
    padding-right: 1.5vh;
    color: white;
    display: flex;
    flex-direction: column;
   

   


}

.partners {
    text-align: -webkit-center;
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    margin: 3vh;
    
}


.imageone {
    margin-right: -5px;
    
}

.imagetwo {
    margin-left: -5px;
}



@media(max-width:900px){
    
.section_two{
    flex-direction: column;
}
   
    .section_two > p{
        font-size: 1.5vh;
        left: 5%;
        right: 5%;
        text-align: center;
        align-items: center;
        margin-left: 68vh;
        margin-right: 68vh;
    }
    .section_two > span >p {
        font-size: 1.5vh;
        
        text-align: center;
        align-items: center;
        margin-left: 68vh;
        margin-right: 68vh;
    }
    
}