@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');

*{
    font-family: 'Open Sans', sans-serif;

}

.header {
    height: 40px;
    display: flex;
    align-items: center;
    background-color: #292929;
    position: sticky;
    top: 0;
    z-index: 100;
    
}

.header_logo {
    letter-spacing: 5px;
   
    font-size: 2.5vh;
    width: 300px;
    display: flex;
    flex-direction: row;
    text-decoration: none;
    margin-left: 10px;
    padding: 2px;
    font-family: 'Open Sans', sans-serif;
    
}



.header_search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
    cursor: none;
}

.header_searchInput {
    height: 12px;
    border: none;
    width: 20%;
    border-radius: 10px;
    cursor: none;
}

.header_searchIcon {
    padding: 5px;
    height: 22px !important;
    background-color: bisque;
    border-radius: 7px;
    margin-left: 5px;
}



.header_optionLineOne {
    font-size: 1.5vw;
    font-family: 'Raleway', sans-serif;
    color: #96f56bb4;
    font-weight: 600;

}

.header_optionLineTwo {
    font-size: 15px;
    font-family: 'Raleway', sans-serif;
    color: #96f56bb4;
    font-weight: 600;

    
    
    
}

.header_optionBasket {
    display: flex;
    align-items: center;
    color: #96f56bb4;
    margin: 5px;
}

.header_optionBasket:hover {
    transform: scale(1.09);
    color: rgb(90, 90, 90);
    letter-spacing: 0.2px;
}

.header_basketCount {
    margin-left: 10px;
    margin-right: 10px;
}

.contact_us, .company {
    color: #96f56bb4;
    text-decoration: none;
    font-size: 14px;
    
    font-family: 'Raleway', sans-serif;
    font-weight: 700;
    display: flex;

}

.Reviews:hover {
    transform: scale(1.09);
    color: rgb(90, 90, 90);
    letter-spacing: 0.2px;

}



.dropdown {
  display: inline-block;
  position: relative;
  color: #96f56bb4;
}
.dropdown-content {
  display: none;
  position: absolute;
  width: 100%;
  overflow: auto;
  box-shadow: 0px 10px 10px 0px rgba(0,0,0,0.4);
}
.dropdown:hover .dropdown-content {
  display: block;
}
.dropdown-content a {
  display: block;
  color: #000000;
  padding: 5px;
  text-decoration: none;
}
.dropdown-content a:hover {
  color: #FFFFFF;
  background-color: #00A4BD;
}

.contact_us:hover, .company:hover {
   
    
    letter-spacing: 0.12rem;
}

.header_nav {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    justify-content: right;
}

.product_list {
    text-decoration: none;
    color: black;
    font-size: 14px;
    font-weight: 700;
    justify-content: center;
    align-items: center;
    display: flex;
    margin: 10px;
}

.product_list:hover {
    letter-spacing: 0.12rem;
}

.right_head, .reviews {
    color: #96f56bb4;
    text-decoration: none;
    font-size: 14px;
    margin: 5px;
    
    font-weight: 600;
    display: flex;
    font-size: auto;
    letter-spacing: 0.08rem;
    
    align-items: center;
    flex: 0.2;
    margin: 2vh;
    

}

.reviews {
    letter-spacing: 0.12rem;
    margin-right: 0.20rem;

}



.header_option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}

.overviewd {
    background-color: aliceblue;

}

.ourvalues {
    background-color: aliceblue;

}

.our_customers {
    background-color: aliceblue;

}

.ourjourneyi {
    background-color: aliceblue;

}

.contact_us {
    font-family: 'Open Sans', sans-serif;

}

.btn-shine {
  position: absolute;
  top: 50%;
  left: 7%;
  transform: translate(-50%,-50%);
  padding: 12px 48px;
  color: #ffffff;
  background: linear-gradient(to right, #4d4d4d 0, white 10%, #4d4d4d 20%);
  background-position: 0;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 3s infinite linear;
  animation-fill-mode: forwards;
  -webkit-text-size-adjust: none;
  font-weight: 600;
  font-size: 2.6vh;
  letter-spacing: 4px;
  text-decoration: none;
  white-space: nowrap}
    
@keyframes shine{
  0%{
    background-position: 0;}
  60%{
    background-position: 180px;}
  100%{
    background-position: 180px;}}



@media(max-width:900px){
    .btn-shine{
        font-size: 1.5vh;
        margin-left: 4%;
    }

    .reviews{
        font-size: 1.2vh;
    }

    .company{
        font-size: 1.2vh;
    }
    .product_list{
        font-size: 1.2vh;
    }
    .contact_us{
        font-size: 1.2vh;
    }
}    