@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap');


.home-row .productone{
    display: flexbox;
    align-items: center;
    height: 100%;
    width: 100%;


}



.headerprod {
	width: device-width;
	width: 100%;
	height: 3vw;
	background-color: black;
	color: #96f56bb4;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1.3vw;
	text-transform: uppercase;
	border-top: 1px solid rgba(53, 53, 53, 0.726);
	
	
	
    margin-bottom: 10px;
}
.headerprodone{
	
	width: 100%;
	height: 3vw;
	background-color: black;
	color: #96f56bb4;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1.5vw;
	text-transform: uppercase;
	position: absolute;
	
	
	
	
    margin-bottom: 10px;
}

.twoheaderprod {
	width: 100%;
	height: 3vw;
	background-color: #3f96cd;
	color: #96f56bb4;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1.5vw;
	text-transform: uppercase;
	letter-spacing: 5px;
	font-weight: 400;
    margin-bottom: 10px;
}

.productcat {
	display: inline-flex;
}
