.checkout {
    display: flex;
    align-items: center;
    justify-content: center;
    
    background-color: white;
    height: max-content;
    width: 100%;
    
    
}



.checkout_left {
    margin-left: 60px;
}


.checkout_title {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgrey;
}

.checkout_right {
    height: auto;
    width: 100%;
    margin-left: 5px;
    align-items: flex-end;
    padding: 5px;

}


