.CheckoutProduct {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: 70px;
    border: 1px solid;
    border-radius: 5px;
    background-color: rgb(248, 239, 228);
    height: auto;
    width: 80%;
  
    
}

.checkoutproduct_info {
    padding-left: 30px;
    margin-bottom: 20px;
    font-size: 12px;
    max-width: 250px;
    
}

.checkoutProduct_info > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}

.checkoutProduct_image {
    object-fit: contain;
    width: 120px;
    height: 140px;
    margin: 10px;
    
    
    
    
}

.checkoutProduct_rating {
    display: flex;
}

.checkoutProduct_title {
    font-size: 12px;
    font-weight: 600;
    margin-top: 20px;
    display: flex;
    
}

.Remove_button {
    
    
    border-radius: 3px;
    
}

