@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap');

    
.read-or-hide {
    color: rgb(219, 218, 218);
    
    cursor: pointer;
}

.container {
    font-family: 'Raleway', sans-serif;
    font-size: 10px;
    letter-spacing: 0.8px;
    

}



div.container-left {
    width:100%;
    overflow:hidden;
    height:auto;
    
  }

  .container-left > p > h2 {
    display: flex;
    justify-content: center;
    align-items: center;
    
  }
  .container-left > p > h2 > img {
    margin: 5px;
  }


  .container-left > p > strong  {
    display: flex;
    justify-content: center;
    align-items: center;
    
  }
  .container-left > p > strong > img  {
    margin: 5px;
    margin-bottom: 10px;
    
    
  }
  div.container-left p {
    
    background-color: #000000;
    padding: 8px;
    border-radius: 10px;
    
  }

  div.container-left h5 {
    animation: slide-left 2s;
    background-color: #ffffff;
    padding: 10px;
    color: black;
    
  }
  div.container-left h6 {
    
    background-color: #ffffff;
    padding: 10px;
    font-size: small;
    
  }

