.login {
    background-color: rgb(209, 208, 208);
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login_logo {
    letter-spacing: 5px;
    color: black;
    font-size: 25px;
    width: 300px;
    display: flex;
    text-decoration: none;
    margin-left: 10px;
    padding: 2px;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    
}

.login_container {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid rgb(145, 145, 145);
    padding: 20px;
}

.login_container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
}

.login_container > form > h5 {
    margin-bottom: 5px;
}

.login_container > form > input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.login_container > p {
    margin-top: 15px;
    font-size: 12px;
}

.login_signInButton {
    background: wheat;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734, #9c7e31, #846a29;
    color: #111;
    border-radius: 5px;
}

.login_registerButton {
    background: wheat;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734, #9c7e31, #846a29;
    color: #111;
    border-radius: 5px;

}

