.closebtn {
    cursor: pointer;
    margin-top: 50px;
    color: rgb(255, 255, 255);
    text-align: end;
    padding-left: 2vh;
    padding-right: 2vh;
    background-color: rgb(228, 65, 65);
    border-radius: 5px;
}

.popdesign {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-Color: rgb(255, 255, 255);
    z-Index: 50;
    padding-left: 30px;
    padding-right: 30px;
    height: fit-content;
    width: fit-content;
   
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    border-radius: 15px;
    border: 2px solid rgb(35, 66, 21);
    scroll-behavior: smooth;
}

@media(max-width:900px){
    .popdesign{
        margin-top:7vh;
        margin-bottom: 10vh;
    }
}